.home-page{
    overflow: hidden;
}
.home-page .banner{
    clip-path: polygon(0 0, 100% 0, 100% 82%, 61% 100%, 0 84%);
    background: linear-gradient(rgba(0,0,0,0.7), rgba(0,0,0,0.7)), url('../../../../assets/Home/banner.png');
    background-repeat: no-repeat;
    background-size: cover;
    background-attachment: fixed;
    position: relative;
    padding:15% 62.5% 15% 10%;
    color:#fff;
    z-index: 1;
    h1{
        font-size: 45px;
    }
    p{
        font-size: 18px;
        margin:30px 0 40px;
    }
    .links{
        display: flex;
    }
    a{
        color:#fff;
        display: flex;
        align-items: center;
        width: fit-content;
        border-radius: 27px;
        padding-left: 25px;
    }
    .link1{
        background-color: #427CFF;
        margin-right: 20px;
        span{
            background:linear-gradient(rgba(0,0,0,0.2), rgba(0,0,0,0.2))
        }
        &:hover{
            background-color: #2868fd;
        }
    }
    .link2{
        background:linear-gradient(rgba(255,255,255,0.2), rgba(255,255,255,0.2));
        span{
            background:linear-gradient(rgba(255,255,255,0.4), rgba(255,255,255,0.4));
        }
        &:hover{
            background:linear-gradient(rgba(255,255,255,0.15), rgba(255,255,255,0.15));
            span{
                background:linear-gradient(rgba(255,255,255,0.3), rgba(255,255,255,0.3));
            }
        }
    }
    span{
        width: 54px;
        height:54px;
        border-radius: 50%;
        display: flex;
        align-items: center;
        justify-content: center;
        font-size: 25px;
        margin-left: 25px;
    }
}
.home-page  .banner-container{
    position: relative;
    overflow: hidden;
    width: 100%;
    .circle{
        position: absolute;
        width: 22%;
        padding-top: 22%;
        border-radius: 50%;
        background: linear-gradient(rgba(35, 57, 109, 0.5),rgba(35, 57, 109, 0.5));
        z-index: 2;
        right: -5%;
        bottom:2%;
    }
}

@media(max-width:1600px){
    .home-page .banner{
        padding:13% 50% 13.5% 10%;
    }
}

@media(max-width:1401px){
    .home-page .banner{
        padding:15% 50% 15% 10%;
        h1{
            font-size: 35px;
        }
        p{
            font-size: 15px;
            margin:25px 0 35px;
        }
        a{
            border-radius: 23px;
            padding-left: 25px;
        }
        .link1{
            margin-right: 20px;
        }
        span{
            width: 46px;
            height:46px;
            font-size: 22px;
            margin-left: 20px;
        }
    }
    .home-page  .banner-container{
        .circle{
            width: 22%;
            padding-top: 22%;
            right: -5%;
            bottom:2%;
        }
    }
}

@media(max-width:1020px){
    .home-page .banner{
        padding:15% 35% 15% 10%;
    }
}

@media(max-width:767px){
    .home-page .banner{
        clip-path: polygon(0 0, 100% 0, 100% 90%, 61% 100%, 0 92%);
        padding:125px 7.5% 100px;
        text-align: center;
        h1{
            font-size: 22.5px;
        }
        p{
            font-size: 14.5px;
            margin:20px 0 30px;
        }
        .links{
            flex-wrap: wrap;
            justify-content: center;
            position: relative;
            z-index: 3 !important;
        }
        a{
            border-radius: 27px;
            padding-left: 17.5px;
        }
        .link1, .link2{
            margin: 5px 10px;
        }
        span{
            width: 40px;
            height:40px;
            font-size: 20px;
            margin-left: 15px;
        }
    }
    .home-page  .banner-container{
        .circle{
            width: 25%;
            padding-top: 25%;
            right: -7.5%;
            bottom:2%;
        }
    }
}

@media(max-width:455px){
    .home-page  .banner-container{
        .circle{
            width: 35%;
            padding-top: 35%;
            right: -5%;
            bottom:2%;
        }
    }
}

@-webkit-keyframes hvr-icon-buzz {
    50% {
      -webkit-transform: translateX(3px) rotate(2deg);
      transform: translateX(3px) rotate(2deg);
    }
    100% {
      -webkit-transform: translateX(-3px) rotate(-2deg);
      transform: translateX(-3px) rotate(-2deg);
    }
  }
  @keyframes hvr-icon-buzz {
    50% {
      -webkit-transform: translateX(3px) rotate(2deg);
      transform: translateX(3px) rotate(2deg);
    }
    100% {
      -webkit-transform: translateX(-3px) rotate(-2deg);
      transform: translateX(-3px) rotate(-2deg);
    }
  }
.home-page .banner{
    a{
        -webkit-transform: perspective(1px) translateZ(0);
        transform: perspective(1px) translateZ(0);
        box-shadow: 0 0 1px rgba(0, 0, 0, 0);
        -webkit-transition-duration: 0.3s;
        transition-duration: 0.3s;
    }
    a svg{
        -webkit-transform: translateZ(0);
        transform: translateZ(0);
    }
    a:hover svg, a:focus svg, a:active svg{
        -webkit-animation-name: hvr-icon-buzz;
        animation-name: hvr-icon-buzz;
        -webkit-animation-duration: 0.35s;
        animation-duration: 0.35s;
        -webkit-animation-timing-function: linear;
        animation-timing-function: linear;
        -webkit-animation-iteration-count: infinite;
        animation-iteration-count: infinite;
    }
}