.home-page .manage-passwords .row1{
    margin:5% 0;
    padding:0 10%;
    h1{
        font-size: 35px;
        margin-bottom: 20px;
    }
    .small-cards{
        display: flex;
        justify-content: space-between;
        flex-wrap: wrap;
    }
    .svg-card{
        width: 28%;
        padding:20px 25px;
        margin:15px 0;
    }
}

@media(max-width:1401px){
    .home-page .manage-passwords .row1{
        h1{
            font-size: 30px;
            margin-bottom: 15px;
        }
        .svg-card{
            width:29%;
            padding:15px 20px;
            margin:10px 0;
        }
    }    
}

@media(max-width:900px){
    .home-page .manage-passwords .row1{
        padding:0 7.5%;
        .svg-card{
            width:45%;
        }
    }    
}
@media(max-width:500px){
    .home-page .manage-passwords .row1{
        margin:40px 0;
        h1{
            font-size: 20px;
            margin-bottom: 5px;
            text-align: center;
        }
        .svg-card{
            width: 100%;
            padding:15px;
            margin:5px 0;
        }
    }    
}


//==================================
.home-page .manage-passwords .shared-row{
    display: flex;
    align-items: center;
    .info{
        width: 45%;
        padding:0 5% 0 10%;
    }
    .img{
        width: 55%;
        transition: 0.35s ease-in-out;
    }
    .img1{
        content: url('../../../../assets/Home/img1.png');
        clip-path: polygon(0 20%, 100% 20%, 100% 100%, 18% 100%);
        &:hover{
            clip-path: polygon(0 20%, 100% 20%, 100% 100%, 0 100%);
            transform: scale(0.8);
        }
    }
    .img2{
        content: url('../../../../assets/Home/img2.png');
        clip-path: polygon(0 0, 100% 0, 82% 80%, 0 80%);
        &:hover{
            clip-path: polygon(0 0, 100% 0, 100% 80%, 0 80%);
            transform: scale(0.8);
        }
    }
    svg{
        font-size: 60px;
        color:#427CFF;
    }
    .info h6{
        font-size: 25px;
        margin:20px 0;
        font-size: 27.5px;
    }
    .info p{
        font-size: 19px;
        margin-bottom: 30px;
    }
}

@media(max-width:1401px){
    .home-page .manage-passwords .shared-row{      
        svg{
            font-size: 50px;
        }
        .info h6{
            font-size: 20px;
            margin:15px 0;
        }
        .info p{
            font-size: 15px;
            margin-bottom: 20px;
        }
    }
}

@media(max-width:700px){
    .home-page .manage-passwords .shared-row{
        flex-wrap: wrap;
        margin:20px 0;
        .info{
            width: 100%;
            padding:0 7.5%;
            margin:20px 0 20px 20px;
        }
        .img{
            width: 100%;
            margin:20px 0;
        }
        .img1{
            clip-path: polygon(0 0, 100% 0, 100% 100%, 25% 100%);
        }
        .img2{
            clip-path: polygon(0 0, 100% 0, 75% 100%, 0 100%);
        }
        svg{
            font-size: 40px;
        }
        .info h6{
            font-size: 17.5px;
            margin:15px 0;
        }
        .info p{
            font-size: 14px;
            margin-bottom: 20px;
        }
        .row2{
            flex-direction: column-reverse;
        }
    }
}
